@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk&display=swap);
::-webkit-input-placeholder {
  font-family: "Space Grotesk", Courier, monospace, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:-ms-input-placeholder {
  font-family: "Space Grotesk", Courier, monospace, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
input,
button,
::placeholder {
  font-family: "Space Grotesk", Courier, monospace, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body {
  scroll-behavior: smooth;
  padding: 5px;
}

body {
  margin: 0;
  background: linear-gradient(89deg, #553d8d 0%, #006eff 100%);
}

button {
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 100px #006eff inset !important;
}

input:-webkit-autofill {
  -webkit-text-fill-color: #fff !important;
}

.App {
  text-align: center;

  overflow-x: hidden;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.homeContainer {
  animation: fadeIn;
  animation-duration: 0.5s;
}

.errors-container {
  margin-top: 20px;
}

.error-box {
  display: none;
}

.error-box.active {
  display: flex;
  opacity: 1;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;

  margin: 10px 0px 10px 0px;
  background-color: #fff;
  padding: 0px 15px;
  border-radius: 5px;
  font-weight: bold;

  background-color: #ffdb39;
  color: #272727;
  font-size: 1.2em;
  cursor: default;
}

.error-box.credentials {
  background-color: #ff3939;
  color: #fff;
}

.error-box p {
  width: 95%;
  text-align: start;
}

.close {
  width: 5%;
  height: auto;
  opacity: 0.6;

  cursor: pointer;
}

.errors-container .active {
  display: flex;
  /*animation: fade linear 0.1s;*/
  animation: fadeInTopRight;
  animation-duration: 0.2s;
}

.info-message {
  color: #fff;
}

.sign-btn {
  color: #fff;
  border-radius: 5px;
  transition: all linear 0.2s;
}

.enable:hover {
  color: #006eff;
  background-color: #fff;
}

.disable {
  cursor: default;
  opacity: 0.5;
}

.Tilt {
    background: linear-gradient(89deg, #553d8d 0%, #006eff 100%);
    display: flex;
    align-items: center;
    justify-content: center;
}

.form {
  width: 700px;

  background-color: rgba(0, 0, 0, 0.25);
  background-image: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 35px,
    rgba(255, 255, 255, 0.5) 35px,
    rgba(255, 255, 255, 0.5) 70px
  );
}

.input-title {
  color: #fff;
  font-weight: bold;
  cursor: default;
}

input,
button {
  outline: none;
  color: #006eff;
  font-weight: bold;
  background-color: rgba(255, 255, 255, 0.8);

  border: 2px solid #006eff;
  border-radius: 5px;
  text-align: center;
}

.input-field::-webkit-input-placeholder {
  color: #006eff;
}

.input-field:-ms-input-placeholder {
  color: #006eff;
}

.input-field::placeholder {
  color: #006eff;
}

.input-field {
  margin-right: 25px;
}

.input-btn {
  opacity: 0.5;
  transition: all linear 0.3s;
}

.input-btn:hover {
  opacity: 1;
}

.image {
  border-radius: 5px;
}

.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #4f14f2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

.bounding-box:hover {
  background-color: rgba(97, 39, 255, 0.25);
}

.signin-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 100%;

  animation: slideInRight;
  animation-duration: 0.5s;
}

.signin-box {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;

  border: none;
  border-radius: 10px;

  background-color: rgba(0, 0, 0, 0.1);
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.3);

  width: 700px;
  /*height: 300px;*/
}

.signin-box h1 {
  color: #fff;
  font-weight: bold;
  font-size: 3em;
}

.horizontal-line {
  display: none;
  margin-bottom: 20px;
}

.signin-inputs-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.signin-inputs-div input {
  background-color: transparent;
  font-size: 1.2em;
  letter-spacing: 3px;
  border: 2px solid #006eff;
  border-radius: 5px;
  padding: 10px 20px;
  margin: 10px 0px 10px 0px;
  width: 60%;
  transition: all ease-in-out 0.1s;
  color: #fff;
}

::-webkit-input-placeholder {
  color: #fff;
}

:-ms-input-placeholder {
  color: #fff;
}

::placeholder {
  color: #fff;
}

.signin-inputs-div input:focus {
  transform: scale(1.05);
}

.sign-in-buttons-div {
  display: flex;
  justify-content: center;
  align-items: center;

  margin: 40px 0px 20px 0px;
  width: 100%;
}

.sign-in-buttons-div button:first-of-type {
  width: 20%;
  border-width: 10px 1px 0px 0px;
  border-radius: 10px 0px 0px 10px;
}

.sign-in-buttons-div button {
  background-color: #fff;
  padding: 5px 10px;
  width: 40%;
  font-size: 1.2em;

  border: 2px solid #006eff;
  border-width: 10px 0px 0px 1px;
  border-radius: 0px 10px 10px 0px;

  transition: all linear 0.1s;
}

.sign-in-buttons-div button:hover {
  color: #fff;
  background-color: #006eff;
}

@media (max-width: 800px) {
  .signin-box {
    width: 650px;
  }

  .signin-inputs-div input {
    width: 80%;
  }

  .sign-in-buttons-div button:first-of-type {
    width: 35%;
  }

  .sign-in-buttons-div button {
    width: 45%;
  }
}

@media (max-width: 690px) {
  .signin-box {
    width: 90%;
  }

  .horizontal-line {
    display: block;
    width: 50%;
    height: 2px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.5);
  }

  .sign-in-buttons-div {
    flex-direction: column;
  }

  .sign-in-buttons-div button:first-of-type {
    width: 80%;
    border-width: 2px;
    margin-bottom: 2px;
    border-radius: 5px;
  }

  .sign-in-buttons-div button {
    width: 80%;
    border-width: 2px;
    border-radius: 5px;
    padding: 10px 15px;
  }
}

